	module.exports = {"data":{"site":{"siteMetadata":{"title":"Apifarms","author":"ApiFarms"}},"markdownRemark":{"id":"/Users/Alex-TBS/Documents/code/bee-project/src/pages/host-a-hive/index.md absPath of file >>> MarkdownRemark","html":"<p>ApiFarms is all about bringing the beekeeping experience directly to you: whether you’re a homeowner, a farmer, or know somewhere in the community that could benefit from biodiversity. We offer personal, industrial and community based partnerships and we are able to install hives in lots of different locations such as community gardens, orchards, rooftops, backyards and parks.</p>\n<p>Hosting a hive with ApiFarms is as easy as one two tree. </p>\n<h3>1. Inspection</h3>\n<p>First, we’ll inspect your location to ensure it provides a favorable environment for the bees. We’ll also start the registration with the Ontario Ministry of Agriculture, Food and Rural Affairs.</p>\n<h3>2. Installation</h3>\n<p>Once the optimal bee hive placements are agreed upon, we transport the hives, bees, seeds (if applicable) and equipment to your location. In addition, all honey collected is reflective of the local biodiversity in your neighbourhood - a unique flavour, homegrown.</p>\n<h3>3. Maintenance</h3>\n<p>ApiFarms fully maintains the installed bee hives, with frequent checkups (every ten to fourteen days). We evaluate colony health and growth, prevent contaminations, expand the hive and most deliciously, collect the honey. All of your honey is packaged, labeled and graded.</p>\n<p>We also have a <a href=\"./seeds\">selection of pollinator friendly flower seeds</a> that are available for anybody who wants to help feed the declining population of pollinator insects while planting a beautiful garden in their yard. Please visit the flower seed section if you are interested, we have great extremely low maintenance flowers available for all kinds of pollinators.\nThe packages we offer start at $450 paid in multiple payments or $400 for a full payment for a full year and we can keep adding hives and perks depending on your wanted needs. Please don’t hesitate to <a href=\"./contact/\">contact us</a> for more information.</p>","frontmatter":{"title":"Host a hive","date":"December 10, 2017","exclude_hr":null,"exclude":null,"image":null}}},"pathContext":{"slug":"/host-a-hive/"}}

